export default {
	data() {
		return {
			date:'2019年12月',
		}
	},
	methods: {
		bindDateChange: function(e) {
			//转化时间格式
			var oDate = new Date(e.target.value);
			//拼接年月
			this.date = oDate.getFullYear()+'年'+(oDate.getMonth()+1)+'月'
		},
	}
}
